import Vue from 'vue'
import Router from 'vue-router'

// 主页面
const Home = r => require.ensure([], () => r(require('@/pages/Home/home')), 'home');
// 创建问卷页面
const Create = r => require.ensure([], () => r(require('@/pages/Create/create')), 'create');
// 预览问卷页面
const Preview = r => require.ensure([], () => r(require('@/pages/Preview/preview')), 'preview');
// 未发布问卷编辑特免edit-create
const Editcreate = r => require.ensure([], () => r(require('@/pages/Edit-create/edit-create')), 'edit-create');
// 发布页面
const Issue = r => require.ensure([], () => r(require('@/pages/Issue/issue')), 'issue');
Vue.use(Router)
const routes = [{
        path: '/',
        name: 'home',
        component: Home
    },
    {
        path: '/home',
        name: 'home',
        component: Home
    },
    {
        path: '/create',
        name: 'create',
        component: Create
    },
    {
        path: '/edit-create',
        name: 'edit-create',
        component: Editcreate
    },
    {
        path: '/preview',
        name: 'preview',
        component: Preview
    },
    {
        path: '/issue',
        name: 'issue',
        component: Issue
    }
];

const router = new Router({
    mode: 'history', //去掉url中的#
    routes
})


export default router


