<style scoped lang="scss">
@import "./index.scss";
</style>
<template>
  <div class="toolbar">
    <nav>
      <ul>
        <li>
          <span>韩创科技</span>
          <span class="el-icon-arrow-right"></span>
          <span @click="go_home">我的问卷</span>
        </li>
        <li>
          <img src="//pic2.hanmaker.com/staff/small/20180612/5b1f45c078cdc.png">
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "toolbar",
  data() {
    return {};
  },
  methods: {
    go_home: function() {
      this.$router.push({ path: "/home" });
    }
  }
};
</script>




